<template>
    <div class="cartcontrol">
        <transition name="move">
            <div class="cart-decrease" v-show="food.count>0" @click="decreaseCart()" >
                <span class="inner el-icon-remove-outline"></span>    
            </div>      
        </transition>
        <div class="cart-count" v-show="food.count>0">{{food.count}}</div> 
        <div class="cart-add el-icon-circle-plus" @click.stop="addCart"></div>
    </div>
</template>

<script>
import Vue from 'vue';

export default {
    // vue在新增删一个陌生字段如这里的food.count,他的变化需要靠vue的set接口监听
    props: {
        food: {
            type:Object
        }
    },
    // created() {
    //     console.log(this.food);
    // },
    methods: {
        addCart(event) {
            console.log("click");
            if (!event._constructed) {
                return;
            }
            if(!this.food.count){
                Vue.set(this.food,'count',1);
            }else{
                this.food.count++
            }
            //这句话的意思是提交名为'cart-add'的事件给父组件,第二个是要传递的参数;
            //通过emit触发父组件goods中的cartAdd方法，并将event.target对象传入
            this.$emit('cart-add',event.target)
        },
        decreaseCart() {
            if(this.food.count){
                this.food.count--;
            }
        }
    }
}

</script>
<style lang='stylus' rel='stylesheet/stylus'>
.cartcontrol
    font-size 0
    .cart-decrease
        display inline-block
        padding 6px
        // transition all .4s linear
        //移动到此位置需要的时间，离开到指定位置所需时间
        &.move-enter-active,&.move-leave-active
            transition: all .4s linear
        &.move-enter,&.move-leave-to
            opacity: 0;
            transform translate3d(24px,0,0)
            .inner
                transform rotate(180deg)
        .inner
            display inline-block
            line-height 24px
            font-size 22px
            color rgb(0,160,220)  
            transition: all .4s linear
    .cart-count
        display inline-block
        vertical-align top
        width 12px
        padding-top 6px
        line-height 24px
        text-align center
        font-size 10px
        color rgb(147,153,159)
    .cart-add
        line-height 24px
        font-size 22px
        padding 6px
        color rgb(0,160,220)
</style>


