<!--  -->
<template>
  <div class="header">
    <!-- <div class="background">
			<img :src="seller.avatar">
		</div> -->
    <div class="content-wrapper">
      <div class="avatar">
        <img :src="seller.avatar" alt="" width="64" height="64">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <!-- 如果这里没有if判断的话，app.vue中的data(){ return seller:{}}一开始是seller
        空的，这会导致seller.supports undefined 是不是数据是数组就要判断？-->
        <div v-if="seller.supports" class="support">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div v-if="seller.supports" class="support-count" @click="showDetail">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="el-icon-arrow-right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetail">
        <span class="bulletin-title"></span>
        <span class="bulletin-text">{{seller.bulletin}}</span>
        <i class="el-icon-arrow-right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" alt="" width="100%" height="100%">
    </div>
    <transition name="fade">
      <div v-show="detailShow" class="detail">
          <div class="detail-wrapper clearfix">
            <div class="detail-main">
              <h1 class="name">{{seller.name}}</h1>
              <div class="star-wrapper">           
                <star :size="48":score="seller.score"></star>
              </div>
              <tit :tit="msg"></tit>
              <ul v-if="seller.supports" class="supports">
                  <li class="support-item" v-for="(v,i) in seller.supports">
                    <span class="icon" :class="classMap[seller.supports[i].type]"></span>
                    <span class="text">{{seller.supports[i].description}}</span> 
                  </li>
              </ul>
              <tit :tit="msg1"></tit>
              <div class="bulletin">
                  <p class="content">{{seller.bulletin}}</p>
              </div>
            </div>
          </div>
          <div class="detail-close" @click="closeDetail">
            <i class="el-icon-close"></i>
          </div>
      </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from '../star/star'
  import tit from '../title/title'

export default {
  //接收从外部传来的值app.vue :seller
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      detailShow:false,
      msg:"优惠信息",
      msg1:"商家公告"
    }
  },
  methods: {
    showDetail() {
      this.detailShow = true;
    },
    closeDetail() {
      this.detailShow = false;
    }
  },
 created(){
    this.classMap = ['decrease','discount','guarantee','invoice','special'];
  },
  components:{
    star,tit
  }
}

</script>
<style lang='stylus' rel="stylesheet/stylus">
  @import "../../common/stylus/mixin"
  @import "../../common/stylus/base"

.header
  color: #fff
  background: rgba(7,17,27,0.5)
  position relative
  overflow hidden
  .content-wrapper
    display :flex
    position relative
    padding:24px 12px 18px 24px
    .avatar
      display inline-block
      vertical-align top
      img 
        border-radius 2px
    .content
      margin-left 16px
      font-size 14px 
      .title  
        margin :2px 0 8px 0
        .brand
          width :30px
          height :18px
          display :inline-block
          vertical-align top
          bg-image('../img/brand')
          background-size:30px 18px
          background-repeat:no-repeat 
        .name
          margin-left 6px
          font-size 16px 
      .description
        margin-bottom 10px
        line-height 12px
        font-size 12px
      .support
        display flex
        align-items center
        .icon
          width 12px
          height 12px
          margin-right 4px
          background-size:12px 12px
          background-repeat:no-repeat 
          &.decrease      
            bg-image('../img/decrease_1')
          &.discount     
            bg-image('../img/discount_1')
          &.guarantee     
            bg-image('../img/guarantee_1')
          &.invoice     
            bg-image('../img/invoice_1')
          &.special     
            bg-image('../img/special_1')
        .text
            line-height 2px
            font-size 10px
    .support-count
      position absolute
      right 12px
      bottom 18px
      padding 0 8px
      padding-left: 12px;
      height 24px
      line-height 24px
      border-radius 14px
      background rgba(0,0,0,0.2)
      text-align center
      display flex
      align-items center
      .count
        font-size 10px
        .el-icon-arrow-right
          font-size 10px
  .bulletin-wrapper
    position relative
    height 28px
    line-height 28px
    padding 0 22px 0 12px
    white-space nowrap
    overflow hidden
    text-overflow ellipsis
    background rgba(7,17,27,0.2)
    .bulletin-title
      display inline-block
      width 22px
      height 12px
      bg-image('../img/bulletin')
      background-size 22px 12px
      background-repeat no-repeat
    .bulletin-text
      vertical-align top 
      font-size 10px
      margin  0 4px 
      position relative
      top -1px
    .el-icon-arrow-right
      font-size 5px
      position absolute
      right 5px
      bottom 8px
  .background
    position absolute
    top 0
    bottom 0
    right 0
    left 0
    z-index -1
    width 100%
    height 100%
    filter blur(10px)
  .detail
    position fixed
    z-index 100
    top 0
    left 0
    width 100%
    height 100%
    overflow auto 
    // 此特性仅限ios
    backdrop-filter blur(10px)
    background rgba(7,17,27,0.8)
    &.fade-enter-active,&.fade-leave-active
        transition: all .5s linear
    &.fade-enter-active
        opacity: 1;
        background rgba(7,17,27,0.8) 
    &.fade-leave-active
        opacity: 0;
        background rgba(7,17,27,0) 
    &.fade-enter,&.fade-leave-to
        opacity: 0;
        background rgba(7,17,27,0)
    .detail-wrapper
      min-height 100%
      width 100%
      .detail-main
        margin-top 64px
        padding-bottom 64px
        .name
          line-height 16px
          text-align center
          font-size 16px
          font-weight  700
        .star-wrapper
          margin-top 18px
          padding 2px 0 
          text-align center
        .supports
          width 80%
          margin 0 auto 
          .support-item
            padding:0 12px
            margin-bottom 12px
            &:last-child
              margin-bottom 0
            .icon
              display inline-block
              width 16px
              height 16px
              vertical-align top
              margin-right 6px
              background-size 16px 16px
              background-repeat no-repeat
              &.decrease      
                bg-image('../img/decrease_2')
              &.discount     
                bg-image('../img/discount_2')
              &.guarantee     
                bg-image('../img/guarantee_2')
              &.invoice     
                bg-image('../img/invoice_2')
              &.special     
                bg-image('../img/special_2') 
            .text
              font-size 12px
              line-height 16px
        
    
        .bulletin
          width 80%
          margin 0 auto
          .content
            padding 0 12px
            line-height 24px
            font-size 12px 
    .detail-close
      position relative
      width 32px
      height 32px
      margin -64px auto
      clear both
      font-size 32px 
</style>


