<!--  -->
<template>
  <div class="ratings" ref="ratings"> 
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left"> 
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.rankRate}}</div>
        </div>
        <div class="overview-right"> 
          <div class="score-wrapper">
              <span class="title">服务态度</span>  
              <star :size="36" :score="seller.serviceScore"></star> 
              <div class="score">{{seller.serviceScore}}</div> 
          </div> 
          <div class="score-wrapper">
              <span class="title">商品评分</span>  
              <star :size="36" :score="seller.foodScore"></star>  
              <div class="score">{{seller.foodScore}}</div> 
          </div>
          <div class="score-wrapper">
              <span class="title">送达时间</span>  
              <span class="deliveryTime">{{seller.deliveryTime}}分钟</span>  
          </div>
        </div>
      </div>
      <split></split> 
      <ratingSelect @increment="incrementTotal" :select-type="selectType" :only-content="onlyContent" :ratings="ratings"></ratingSelect>
      <div class="rating-wrapper">
        <ul>
          <li v-for="rating in ratings" class="rating-item" v-show="needShow(rating.rateType,rating.text)">
            <div class="avatar">
              <img :src="rating.avatar" alt="" width="28" height="28">
            </div>
            <div class="content">
              <h1 class="name">{{rating.username}}</h1>
              <div class="star-wrapper">
                <star :size="24" :score="rating.score"></star> 
                <span class="deliveryTime" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span> 
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend || rating.recommend.length">
                <span class="iconfont icon-zan1"></span>
                <span v-for="item in rating.recommend" class="item">{{item}}</span>
              </div>
              <div class="time">{{rating.rateTime | formatDate}}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll  from 'better-scroll'
  import {formatDate} from '../../common/js/date.js'
  import star from '../star/star'
  import split  from '../split/split'
  import ratingSelect  from '../ratingSelect/ratingSelect'

const ALL = 2;
const ERR_OK = 0
export default {
  filters: {
    formatDate: function (time) {
      let date = new Date(time);
      return formatDate(date,'yyyy-MM-dd hh:mm');
    }
  },
  props:{
    seller:{
      type:Object
    }
  },
  data(){
    return{
      ratings:[],
      selectType:ALL,
      onlyContent:true,
    }
  },

  components:{
    star,split,ratingSelect
  },
  methods:{
    needShow(type,text){
        if(this.onlyContent && !text){
            return false;
        }
        if( this.selectType === ALL){
            return true;
        }else{
            return type === this.selectType;
        }
    },
    incrementTotal(type, data){
        this[type] = data;
        this.$nextTick(()=>{
            this.scroll.refresh();
        })
    }
  },
  created() {
    this.$http.get('static/data.json').then((response)=>{
        response = response.body;
        if(response.errno==ERR_OK){
          this.ratings = response.data;
          this.$nextTick(()=>{
            this.scroll = new BScroll(this.$refs.ratings,{
              click:true 
            })
          }) 
        }
    })
  }
}

</script>
<style lang='stylus' rel='stylesheet/stylus'>
@import "../../common/stylus/mixin"
.ratings
  position absolute
  top 174px
  bottom 0
  left 0
  width 100%
  overflow hidden
  .overview
    display flex
    padding 18px 0
    .overview-left
      flex 0 0 137px
      @media only screen and (max-width:320px)
          flex 0 0 100px
      border-right 1px solid rgba(7,17,27,0.1)
      text-align center
      padding 6px 0
       
      .score
        line-height 28px
        font-size 24px
        color rgb(255,153,0)
        margin-bottom 6px
      .title
        font-size 12px
        color rgb(7,17,27)
        line-height 12px
        margin-bottom 8px
      .rank
        font-size 10px
        color rgb(147,159,151)
        line-height 20px
    .overview-right
      flex 1
      padding-left 24px
      padding-top: 6px
      @media only screen and (max-width:320px)
        padding-left 12px
      .score-wrapper
        display flex
        .title
          margin-right 12px
          margin-bottom 8px
          font-size 12px
          line-height 18px
        .deliveryTime
          font-size 12px
          line-height 18px
          color rgb(147,159,151)
        .score
          font-size 12px
          line-height 18px
          color rgb(255,153,0)

  .rating-wrapper
    padding 0 18px
    .rating-item
      display flex
      padding-top 18px
      border-1px(rgba(7,17,27,0.1))
      .avatar img
        border-radius 50%
      .content
        position relative
        flex 1
        margin-left 12px
        .name
          font-size 10px
          line-height 12px
          color rgb(7,17,27)
          margin-bottom 4px
        .star-wrapper
          display flex
          .deliveryTime
            color rgb(147,153,159)
            font-size 10px
            line-height 12px
            font-weight 100
            margin-left 6px
        .text
          font-size 10px
          line-height 18px
          color rgb(7,17,27)
          margin-top 6px
          margin-bottom  8px
        .recommend
          line-height 16px
          .icon-zan1
            font-size 12px
            color rgb(0,160,220)
          .item
            font-size 9px
            border-radius 1px
            border 1px solid rgba(7,17,27,0.1)
            padding 0 6px
            color rgb(147,153,159)
            margin 0 8px 4px 0
            display inline-block
        .time
          color rgb(147,153,159)
          font-size 10px
          line-height 12px
          position absolute
          top 0
          right 18px
          font-weight 100
      
</style>


