<template>
  <div class="star" :class="starType">
      <span v-for="itemClass in itemClasses" :class="itemClass" class="star-item" ></span>
      <!-- <span v-for="itemClass in itemClasses" :class="itemClass" class="star-item" track-by="$index"></span> -->
  </div>
</template>

<script type="text/ecmascript-6">
const LENGTH = 5;
const CLS_ON = 'on';
const CLS_HALF = 'half';
const CLS_OFF = 'off';


export default {
  // 从外部传来星星的分数和尺寸
  props: {
    size: {
      type:Number
    },
    score: {
      type:Number
    }
  },
  computed:{
    // 将尺寸分数类型关联计算到starType class类型上
    starType(){
      return "star-" + this.size
    },
    itemClasses() {
      let result = [];
      let score = Math.floor(this.score*2)/2;
      // 判断是否有小数点
      let hasDeciaml = score % 1 !== 0;
      // 判断有多少颗全星
      let integer = Math.floor(score);
      for(let i=0;i<integer;i++){
        result.push(CLS_ON);
      }
      if(hasDeciaml){
        result.push(CLS_HALF);
      }
      while (result.length < LENGTH){
        result.push(CLS_OFF);
      }
      return result
    }
  }
}

</script>
<style lang='stylus' rel="stylesheet/stylus">
@import "../../common/stylus/mixin"
.star
  font-size 0
  .star-item
    display inline-block;
    background-repeat no-repeat;     
  &.star-48
    .star-item
      width 20px
      height 20px
      margin-right 22px
      background-size 20px 20px
      &.last-child
        margin-right 0
      &.on
        bg-image('../img/star48_on')
      &.half
        bg-image('../img/star48_half')
      &.off
        bg-image('../img/star48_off')
  &.star-36
    .star-item
      width 15px
      height 15px
      // margin-right 16px
      margin-right 10px
      background-size 15px 15px
      &.last-child
        margin-right 0
      &.on
        bg-image('../img/star36_on')
      &.half
        bg-image('../img/star36_half')
      &.off
        bg-image('../img/star36_off')
  &.star-24
    .star-item
      width 10px
      height 10px
      margin-right 3px
      background-size 10px 10px
      &.last-child
        margin-right 0
      &.on
        bg-image('../img/star24_on')
      &.half
        bg-image('../img/star24_half')
      &.off
        bg-image('../img/star24_off')
</style>


