<template>
    <transition name="fade">
        <div v-show="showFlag" class="food" ref="food">
            <div class="food-content">
                <div class="image-header">
                    <img :src="food.image">
                    <div class="back" @click="hide()">
                        <span class="el-icon-arrow-left"></span>
                    </div>
                </div>
                <div class="content">
                    <h1 class="title">{{food.name}}</h1>  
                    <div class="detail">
                        <span class="sell-count">月售{{food.sellCount}}</span> 
                        <span class="rating">月售{{food.rating}}%</span>
                    </div> 
                    <div class="price">
                        <span class="now">{{food.price}}</span> 
                        <span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
                    </div>
                    <div class="cartcontrol-wrapper">
                        <cartcontrol :food="food" ></cartcontrol> 
                    </div>   
                    <div class="buy" v-show="!food.count || food.count===0" @click="addFirst">加入购物车</div>    
                </div> 
                <split  v-show="food.info"></split>   
                <div class="info" v-show="food.info">
                    <h1 class="title">商品信息</h1>   
                    <p class="text">{{food.info}}</p> 
                </div> 
                <split></split>   
                <div class="rating">
                    <h1 class="title">商品评价</h1>
                    <ratingSelect @increment="incrementTotal":select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingSelect>    
                    <div class="rating-wrapper">
                        <ul v-show="food.ratings && food.ratings.length">
                            <li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
                                <div class="user">
                                    <span class="name">{{rating.username}}</span>
                                    <img :src="rating.avatar" alt=""width="12" height="12" class="avatar">
                                </div>
                                <div class="time">{{rating.rateTime | formatDate}}</div>
                                <p class="text">
                                    <span  v-show="rating.rateType===0"class="iconfont icon-zan1"></span>
                                    <span  v-show="rating.rateType===1"class="iconfont icon-zan"></span>
                                    <span>{{rating.text}}</span>
                                </p>
                            </li>
                        </ul>       
                        <div class="no-ratings" v-show="!food.ratings || !food.ratings.length">暂无评价</div>    
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import BScroll  from 'better-scroll'
import Vue from 'vue';
import {formatDate} from '../../common/js/date.js'
import cartcontrol  from '../cartcontrol/cartcontrol'
import split  from '../split/split'
import ratingSelect  from '../ratingSelect/ratingSelect'

const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;

export default {  
    filters: {
        formatDate: function (time) {
            let date = new Date(time);
            return formatDate(date,'yyyy-MM-dd hh:mm');
        }
    },
    props: {
        food: {
            type:Object
        }
    },
    data () {
        return {
            selectType:ALL,
            showFlag:false,
            onlyContent:true,
            desc:{
                all:"全部",
                positive:"推荐",
                negative:"吐槽"
            }
        };
    },
    components: {
        cartcontrol,
        split,
        ratingSelect
    },
  methods: {
    show() {
        this.showFlag = true;
        this.selectType = ALL;
        this.onlyContent = true;

        this.$nextTick(() => {
            if(!this.scroll){
                this.scroll =  new BScroll(this.$refs.food,{
                    click:true   
                })
            }else{
                this.scroll.refresh();
            }     
        })
    },
    hide() {
        this.showFlag = false;
    },
    addFirst() {
        console.log("click")
        if(!event._constructed){
            return;
        }
        Vue.set(this.food,'count',1);
        this.$emit("cart.add",event.target);
    },
    needShow(type,text){
        if(this.onlyContent && !text){
            return false;
        }
        if( this.selectType === ALL){
            return true;
        }else{
            return type === this.selectType;
        }
    },
    incrementTotal(type, data){
        this[type] = data;
        this.$nextTick(()=>{
            this.scroll.refresh();
        })
    }
  }
}

</script>
<style lang='stylus' rel='stylesheet/stylus'>
@import "../../common/stylus/mixin"

.food
    position fixed
    left 0
    top 0
    bottom 48px
    z-index 30
    width 100%
    background #fff
    &.fade-enter-active, &.fade-leave-active 
        transition: all 0.5s linear 
        transform translate3d(0, 0, 0) //每个表项相对于当前自身的高度做一个偏移
    &.fade-enter, &.fade-leave-active
        transform translate3d(100%, 0, 0)
    .image-header
        position relative
        width 100%
        height 0
        padding-top 100%
        img 
            position absolute
            top 0
            left 0
            width 100%
            height 100%
        .back
            position absolute
            top 10px
            left 0
            color #fff
            .el-icon-arrow-left 
                display block
                padding 10px
                font-size 20px
                color #fff
    .content
        padding 18px
        position relative
        .title
            line-height 14px
            margin-bottom 8px
            font-size 14px
            font-weight 700
            color rgb(7,17,27) 
        .detail
            margin-bottom 18px
            line-height 10px
            height 10px
            font-size 0
            .sell-count,.rating
                font-size 10px
                color rgb(147,153,159)
            .sell-count
                margin-right  12px 
        .price         
            .now
                color:rgb(240,20,20) 
                font-size 14px
                margin-right 8px
                font-weight 700
                line-height 24px
            .old
                font-size 10px
                color rgb(147,153,159)
                text-decoration line-through

        .cartcontrol-wrapper
            position absolute
            right 12px
            bottom 12px
        .buy
            position absolute
            right 18px
            bottom 18px
            z-index 10
            height 24px
            line-height 24px
            padding 0 12px
            box-sizing border-box
            font-size 10px
            border-radius 12px
            color #ffffff
            background rgb(0,160,220)
    .info
        padding 18px
        .title 
            line-height 14px
            margin-bottom 6px
            font-size 14px
            color rgb(7,17,27)
        .text
            color rgb(77,85,93)
            line-height 24px
            padding 0 8px
            font-size 12px
    .rating
        padding-top 18px
         
        .title 
            line-height 14px
            margin-left 18px
            font-size 14px
            color rgb(7,17,27)
        .rating-wrapper
            padding 0 18px
            .rating-item
                position relative
                padding 16px 0
                border-1px(rgba(7,17,27,0.1))
                .user
                    position absolute
                    right 0
                    top 16px
                    line-height 12px
                    .name
                        display inline-block
                        vertical-align top
                        font-size 10px
                        margin-right 6px
                        color rgb(147,153,159)
                    .avatar
                        border-radius 50%
                .time
                    font-size 10px
                    margin-bottom 6px
                    color rgb(147,153,159)
                    line-height 12px
                .text
                    line-height 16px
                    color rgb(7,17,27)
                    font-size 12px

            .icon-zan1,.icon-zan
                color #00a0dc
                margin-right 4px
                line-height 16px
                font-size 12px
            .icon-zan
                color #b7bbbf
            .no-ratings
                padding 16px 0
                font-size 12px
                color rgb(147,153,159)

</style>


