<template>
  <div class="ratingSelect">
      <div class="rating-type">
          <div class="block positive" @click="select(2,$event)" :class="{active:myselectType===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></div>
          <div class="block positive" @click="select(0,$event)" :class="{active:myselectType===0}">{{desc.positive}}<span class="count">{{positives.length}}</span></div>
          <div class="block negative" @click="select(1,$event)" :class="{active:myselectType===1}">{{desc.negative}}<span class="count">{{negatives.length}}</span></div>
      </div>
      <div class="switch" @click="toggleContent($event)" :class="{on:myonlyContent}">
          <span class="el-icon-circle-check"></span>
          <span class="text">只看有内容的评价</span>
      </div>
  </div>
</template>

<script>
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;


export default {
    props:{
        ratings:{
            type: Array,
            default() {
                return [];
            }
        },
        selectType:{
            type: Number,
            default: ALL
        },
        onlyContent:{
            type: Boolean,
            default() {
                return true;
            }
        },
        desc:{
            type: Object,
            default() {
                return {
                    all:"全部",
                    positive:"满意",
                    negative:"不满意"
                }
            }
        }
    },
    data() {
        return {
            myselectType : this.selectType,
            myonlyContent: this.onlyContent
        }
    },
    methods:{
        select(type){
            if(!event._constructed){
                return;
            }
            this.myselectType = type;
            this.$emit('increment','selectType',this.myselectType)
        },
        toggleContent(event){
            if(!event._constructed){
                return;
            }
            this.myonlyContent = !this.myonlyContent;
            this.$emit('increment','onlyContent',this.myonlyContent)
        }
    },
    computed:{
        positives(){
            return this.ratings.filter((rating)=>{
                return rating.rateType === POSITIVE;
            })
        },
        negatives(){
            return this.ratings.filter((rating)=>{
                return rating.rateType === NEGATIVE;
            })
        }
    }
}

</script>
<style lang='stylus' rel='stylesheet/stylus'>
@import "../../common/stylus/mixin"
.ratingSelect
    .rating-type
        display flex
        padding 18px 0
        margin 0 18px
        border-1px(rgba(7,17,27,0.1))
        .block
            padding 8px 12px
            margin-right 8px
            border-radius 1px
            color rgb(77,85,93)
            font-size 12px
            line-height 16px
            &.active
                color #fff
            .count
                font-size 8px
                margin-left 2px
            &.positive
                background rgba(0,160,220,0.2)
                &.active
                    background rgb(0,160,220)
            &.negative
                background rgba(77,85,93,0.2)
                &.active
                    background rgb(77,85,93)
    .switch
        padding 12px 18px
        line-height 24px
        border-bottom 1px solid rgba(7,17,27,0.1)
        color rgb(147,153,159)
        display flex
        align-items center
        &.on
            .el-icon-circle-check
                color #00c850
        .el-icon-circle-check
            font-size 24px
            margin-right 4px
        .text
            font-size 12px


</style>


