<template>
    <div class="title1">
        <div class="line"></div>
        <div class="text">{{tit}}</div>
        <div class="line"></div>
    </div>
</template>

<script type="text/ecmascript-6">

export default {
    // 从父组件传过来的:tit
    props: ["tit"]
}
</script>
<style lang='stylus' rel="stylesheet/stylus">
.title1
    display flex
    width 80%
    margin 28px auto 24px
    .line
        flex 1
        position relative 
        top -6px
        border-bottom 1px solid #fff
    .text
        padding 0 12px
        font-weight 700
        font-size 14px
</style>


