<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc">
          <star :size="36" :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>分钟
            </div>
          </li>
        </ul>
        <div class="favorite"  @click="toggleFavorite($event)">
          <span class="iconfont icon-aixin" :class="{'active':favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
      </div>
      <split></split>  
      <div class="bulletin">
          <h1 class="title">公告与活动</h1>
          <div class="content-wrapper">
            <p class="content">{{seller.bulletin}}</p>
          </div>
          <ul v-if="seller.supports" class="support">
            <li v-for="(v,i) in seller.supports" class="support-item border-1px">
              <span class="icon" :class="classMap[seller.supports[i].type]"></span>
              <span class="text">{{seller.supports[i].description}}</span>
            </li>
          </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="(v,i) in seller.pics">
              <img :src="v" class="" alt="" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <div class="title border-1px">商家信息</div>
        <ul>
          <li class="info-item border-1px" v-for="(v,i) in seller.infos">{{v}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll  from 'better-scroll'
import {saveToLocal,loadFromlLocal} from '../../common/js/store.js'

import star from '../star/star'
import split  from '../split/split'

export default {
  props:{
    seller:{
      type:Object
    }
  },
  data(){
    return{
      // favorite:false
      favorite: (() => {
        return loadFromlLocal(this.seller.id, 'favorite', false);
      })()
    }
  },
  computed:{
    favoriteText(){
      return this.favorite?'已收藏':'未收藏'
    }
  },
  components:{
    star,split
  },
  watch:{
    'seller'(){
      this._initScroll();
      this._initPic();

    }
  },
  created(){
    this.classMap = ['decrease','discount','guarantee','invoice','special'];
  },
  methods:{
    toggleFavorite(event){
      if (!event._constructed) {
        return;
      }
      // this.favorite = !this.favorite;
      saveToLocal(this.seller.id, 'favorite', this.favorite)
    },
    _initScroll(){
      if(!this.scroll){
        this.$nextTick(()=>{
          this.scroll = new BScroll(this.$refs.seller,{
            click:true,
          })
        })     
      }else{
        this.scroll.refresh();
      }
    },
    _initPic(){
      // pic-wrapper是固定宽度的视口的大小，当里面的ul超过视口宽度的时候就会出现滚动,这里手动计算出ul宽度
      if(this.seller.pics){
        let picWidth = 120;
        let margin = 6;
        let width = (picWidth+margin)*this.seller.pics.length-6;
        this.$refs.picList.style.width = width+"px"
        this.$nextTick(()=>{
          if(!this.picScroll){
            this.picScroll = new BScroll(this.$refs.picWrapper,{
              scrollX:true,
              eventPassThrough:'vertical'//横向滚动图片的时候就忽略垂直方向的滚动
            })
          }else{
            this.picScroll.refresh();
          }
        })
      }
    }
  },
  mounted(){
    this._initScroll();
    this._initPic();
  }
}

</script>
<style lang='stylus' rel="stylesheet/stylus">
  @import "../../common/stylus/mixin"

.seller
  position absolute
  top 174px
  bottom 0
  left 0
  width 100%
  overflow hidden
  .overview
    position relative
    padding 18px
    .desc
      display flex
      align-items center
      padding-bottom 18px
      border-1px(rgba(7,17,27,0.1))
      .text
        font-size 10px
        color rgb(77,85,93)
        line-height 18px
        margin-right 6px
    .title
      font-size 14px
      color rgb(7,17,27)
      line-height 14px
      margin-bottom 8px
    .remark
      display flex
      padding-top 18px
      .block
        flex 1
        text-align center
        border-right 1px rgba(7,17,27,0.1) solid
        &:last-child
          border-right none
        h2
          font-size 10px
          color rgb(147,153,159)
          line-height 10px
          margin-bottom 4px
        .content
          font-size 10px
          line-height 24px
          font-weight 100
          color rgb(7,17,27)
          .stress
            font-size 24px
    .favorite
      position absolute
      right 18px
      top 18px
      display flex
      flex-direction column
      align-items center
      justify-content center
      .icon-aixin
        font-size 24px
        line-height 24px
        color #d4d6d9
        margin-bottom 4px
        &.active
          color rgb(240,20,20)
      .text
        font-size 10px
        line-height 10px
        color rgb(77,85,93)

  .bulletin
    padding 18px
    .title
      line-height 14px
      color rgb(7,17,27)
      font-size 14px
    .content-wrapper
      color rgb(240,20,20)
      margin-top 8px
      padding-left 12px
      line-height 20px
      font-size 12px
      font-weight 100
      padding-bottom 16px
      border-1px(rgba(7,17,27,0.1))
    .support
      .support-item
        padding 16px 12px
        font-size 12px
        line-height 16px
        color rgb(7,17,27)
        border-1px(rgba(7,17,27,0.1))
        &:last-child
          border-none() 
        .icon
          width 12px
          height 12px
          margin-right 4px
          background-size:12px 12px
          background-repeat:no-repeat 
          display inline-block
          &.decrease      
            bg-image('../img/decrease_4')
          &.discount     
            bg-image('../img/discount_4')
          &.guarantee     
            bg-image('../img/guarantee_4')
          &.invoice     
            bg-image('../img/invoice_4')
          &.special     
            bg-image('../img/special_4')
  .pics
    padding 18px
    .pic-wrapper
      margin-top 12px
      width 100%
      overflow hidden
      white-space nowrap
      .pic-list
        display flex
        .pic-item
          margin-left 6px
          width 120px
          height 90px
  .info
    padding 18px
    .title
      padding-bottom 12px
      border-1px(rgba(7,17,27,0.2))
    .info-item
      padding 16px 12px
      font-size 12px
      font-weight 100
      color rgb(7,17,27)
      line-height 16px
      border-1px(rgba(7,17,27,0.2))
      &:last-child
        border-none()
        
</style>


