<!--  -->
<template>
<div class="goods">
  <!--vue1.0 v-el:dom获得dom对象 this.$els.dom不能驼峰命名-->
  <!--vue2.0 ref="dom"获得dom对象 this.$refs.dom尽量驼峰命名 不要使用'-'-->
  <div class="menu-wrapper" ref="menuWrapper">
    <ul>
      <li v-for="(v,i) in goods" class="menu-item" :class="{'current':currentIndex===i}" @click="selectMenu(i)">
        <div class="text border-1px">
           <span v-show="v.type>0" class="icon" :class="classMap[v.type]"></span>{{v.name}}
        </div>
      </li>
    </ul>
  </div>
  <div class="foods-wrapper" ref="foodWrapper">
    <ul>
      <li v-for="(v,i) in goods" class="food-list food-list-hook">
        <h1 class="title">{{v.name}}</h1>
        <ul>
          <li @click="selectFood(food,$event)" v-for="food in v.foods" class="food-item border-1px">
            <div class="icon">
              <img :src="food.icon" width="57" alt="">
            </div>
            <div class="content">
              <h2 class="name">{{food.name}}</h2>
              <p class="desc">{{food.description}}</p>
              <div class="extra">
                <span class="count">月销{{food.sellCount}}</span>
                <span>好评率{{food.rating}}%</span>
              </div>
              <div class="price">
                <span class="now">￥{{food.price}}</span>
                <span v-show="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
              </div>
              <div class="cartcontrol-wrapper">
                <!-- 购物车组件如果提交了'cart-add'事件就调用这个cartAdd函数; -->
                <cartcontrol :food="food" v-on:cart-add="cartAdd"></cartcontrol> 
              </div>
            </div>
          </li>
        </ul>
      </li>
    </ul>
  </div>
  <shopcart ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart> 
  <food :food="selectedFood" ref="food"></food>
</div>
</template>

<script>
import BScroll  from 'better-scroll'
import shopcart  from '../shopcart/shopcart'
import cartcontrol  from '../cartcontrol/cartcontrol'
import food  from '../food/food'


const ERR_OK =0;
export default {
  props: {
    seller: {
      type: Object
    }
  },
  components: {
    shopcart,
    cartcontrol,
    food
  },
  data() {
    return {
      goods: [],
      listHeight:[],
      scrollY : 0,
      selectedFood: {}
    }
  },
  computed: {
    currentIndex() {
      for(let i = 0; i < this.listHeight.length; i++){
        let height1 =  this.listHeight[i];
        let height2 =  this.listHeight[i+1];
        if(!height2 || (this.scrollY >= height1 && this.scrollY < height2)){
          return i
        }
      }
      return 0;
    },
    selectFoods() {
      let foods = [];
      this.goods.forEach((good) =>{
        //上面遍历的food
        good.foods.forEach((food) =>{
          if(food.count){
            foods.push(food);
          }
        })
      })
      return foods;
    }
  },
  created() {
    this.$http.get('static/data.json').then((response) => {
    // this.$http.get('/api/goods').then((response) => {

      response = response.body;
      if(response.errno == ERR_OK){
        this.goods = response.data;
        // console.log(this.goods);
        this._initScroll()
        // 视频中是用了vue这个接口初始化才成功使用,还是这么使用吧
        // this.nextTick(() => {
        //   this._initScroll()
        // })
        this.$nextTick(() => {
          this._calculateHeight();
        })       
      }
    })
    this.classMap = ['decrease','discount','guarantee','invoice','special'];
  },
  methods: {
    //如果在webpack派发点击事件pc端会执行两次即可传两个参数
    // selectMenu(index，event) {
    //   if(!event._constructed) {
    //   return;
    //   }
    // }
    cartAdd(el) {
      // dom元素更新后执行， 因此此处能正确打印出更改之后的值；
      this.$nextTick(() => {
        //将target传入shopCart子组件中的drop方法，所以drop方法能获得用户点击按钮的元素，即能获取点击按钮的位置
        // 调用shopcart组件的drop()函数
        this.$refs.shopcart.drop(el);
        // this.$refs['shopcart'].drop(target); 也可以这样写
      })
    },
    selectMenu(index) {
      console.log(index);
      let foodList = this.$refs.foodWrapper.getElementsByClassName("food-list-hook"); 
      let el = foodList[index];
      this.foodScroll.scrollToElement(el,300);
    },
    selectFood(food,event) {
      if(!event._constructed) {
        return;
      }  
      this.selectedFood = food;
      this.$refs.food.show();

    },
    _initScroll() {
      // 接收两个参数，前面是dom对象后面是json对象
      this.menuScroll = new BScroll(this.$refs.menuWrapper,{
        click:true   //允许better-scroll列表上的点击事件
      })
      this.foodScroll = new BScroll(this.$refs.foodWrapper,{
        click:true,
        probeType: 3 //让better-scroll监听scroll事件
      })
      this.foodScroll.on("scroll",(pos) => {
        this.scrollY = Math.abs(Math.round(pos.y));
      })
    },
    _calculateHeight() {
      let foodList = this.$refs.foodWrapper.getElementsByClassName("food-list-hook"); 
      let height = 0;        
      this.listHeight.push(height);        
      for(let i = 0;i < foodList.length; i ++) {          
        let item = foodList[i];         
        height += item.clientHeight;         
        this.listHeight.push(height);        
      }
    }
  }
}

</script>
<style lang='stylus' rel="stylesheet/stylus">
  @import "../../common/stylus/mixin"

.goods
  display flex
  position absolute
  // top 174px
  top 178px
  bottom 46px
  overflow hidden
  .menu-wrapper
    // flex三个属性1.等分2.内容不足时的缩放3.占位空间
    flex 0 0 80px
    width 80px
    background #f3f5f7
    .menu-item
      // 垂直居中
      display table
      height 54px
      width 56px
      line-height 14px
      margin 0 auto
      &.current
        position relative
        margin-top -1px
        background #fff
        width 100%
        .text
          font-weight 700
          border_none()
      .text
        display table-cell
        width 56px
        vertical-align middle
        border-1px(rgba(7,17,27,0.1))
        font-size 12px
        text-align center
      .icon
        display inline-block 
        width 12px
        height 12px
        margin-right 2px
        background-size:12px 12px
        background-repeat:no-repeat 
        &.decrease      
          bg-image('../img/decrease_3')
        &.discount     
          bg-image('../img/discount_3')
        &.guarantee     
          bg-image('../img/guarantee_3')
        &.invoice     
          bg-image('../img/invoice_3')
        &.special     
          bg-image('../img/special_3')
      
  .foods-wrapper
    flex 1
    .title
      padding-left 14px
      height 26px
      line-height 26px
      border-left 2px solid #d9dde1
      font-size 12px
      color rgb(147,153,159) 
      background #f3f5f7
    .food-item
      display flex
      margin 18px
      padding-bottom 18px
      border-1px(rgba(7,17,27,0.1))
      &:last-child
        border-none()
        margin-bottom 0
      .icon
        flex 0 0 57px
        margin-right 10px
      .content
        flex 1
        .name
          margin 2px 0 8px 0
          height 14px
          line-height 14px
          font-size 14px
          color rgb(7,17,27)
        .desc,.extra
          line-height 10px
          font-size 10px
          color rgb(147,153,159)
        .desc
          margin-bottom 8px
          line-height 12px
        .extra
          .count
            margin-right 12px
        .price         
          .now
            color:rgb(240,20,20) 
            font-size 14px
            margin-right 8px
            font-weight 700
            line-height 24px
          .old
            font-size 10px
            color rgb(147,153,159)
            text-decoration line-through
        .cartcontrol-wrapper
          position absolute
          right 0
          bottom 12px
</style>


